          setTimeout(function () {
            $('.l8 .li4').addClass('ho')
          },10)
        },function () {
          $('.l8 .li4').removeClass('ho')
          setTimeout(function () {
            $('.l8 .li4 .erwei').css('display','none')
          },10)
        })
        // 
        $(window).scroll(function(event){
          if($(window).scrollTop()>1000){
            $('.l8').addClass('show');
          }else{
            $('.l8').removeClass('show');
          }
        });
      }
