<template>
  <div id="app">
    <header class="header">
      <div class="header-wrap">
        <div class="logo">
          <img src="../static/img/logo.png" alt="">
        </div>
        <nav class="header-nav">
          <ul class="nav-btn clearfloat">
            <li v-for="(item,index) in navList" :key="index" :class="{sel:index == 0}">
              <router-link :to="item.url">{{item.text}}</router-link>
              <ul class="nav-tags">
                <li v-for="(item1,index1) in item.tags" :key="index1">
                  <router-link :to="item1.url">{{item1.tagsText}}</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </nav>
      </div>
    </header>
    <section class="main">
    <router-view/>
    </section>
    <footer class="footer">
      <div class="footer-wrap">
        <div class="clearfloat">
          <div class="footer-left floatl">
            <div class="logo01">
              <img src="../static/img/logo01.png" alt="">
            </div>
            <ul>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div class="footer-right floatr clearfloat">
            <div class="code01 floatl">
              <div class="img"></div>
              <div></div>
            </div>
            <div class="code02 floatl">
              <div class="img"></div>
              <div></div>
            </div>
          </div>
        </div>
      </div>
      <div class="footer-wrap1">
        <span>mi.com ICP110507 ICP10046444 11010802020134 [2017]1530-131</span>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      navList:[
        {text:'',url:'/homePage',tags:[]},
        {text:'',url:'/aboutUs',tags:[{tagsText:'',url:''},{tagsText:'',url:''},{tagsText:'',url:''},{tagsText:'',url:''},{tagsText:'',url:''}]},
        {text:'',url:'/productCenter',tags:[]},
        {text:'',url:'/information',tags:[]},
        {text:'',url:'/service',tags:[{tagsText:'',url:''},{tagsText:'',url:''}]},
        {text:'',url:'/invite',tags:[{tagsText:'',url:''},{tagsText:'',url:'/inviteJoin'}]},
        {text:'',url:'/partner',tags:[{tagsText:'',url:''},{tagsText:'',url:''}]},
      ]
    }
  },
  mounted(){
    // 
    $('.nav-btn>li a').on('click',function () {
      $('.nav-btn>li').removeClass('sel');
      $(this).parent('li').addClass('sel')
    });

  }
}
</script>

<style scoped>
@import "../static/css/public.css";
</style>



